@import url(https://fonts.googleapis.com/css?family=Inconsolata);
body {
  margin: 0;
  padding: 0;
  font-family: 'Inconsolata', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #100C19;
  background-image: -webkit-linear-gradient(#100C19, #44404E);
  background-image: linear-gradient(#100C19, #44404E);
  background-repeat: no-repeat;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

p::-webkit-scrollbar, div::-webkit-scrollbar {
  width: 2px;
  height: 2px;
}

p::-webkit-scrollbar-track, div::-webkit-scrollbar-track {
  background: #ffffff;
}

/* Handle */
p::-webkit-scrollbar-thumb, div::-webkit-scrollbar-thumb {
  background: #E0E1E2;
}

#root {
  height: 100%;
  width: 100%;
}
